<template>
    <div>
        <input class="file" type="file" name="file" webkitdirectory @change="file">
        <button id="upload-btn" type="button">upload</button>
        <!-- <el-dialog
            title="提示"
            :visible.sync="dialogVisible"
            width="30%">
            <el-form>
                <el-form-item label="测试:">
                    <el-input v-model="a"></el-input>
                </el-form-item>
            </el-form>
        </el-dialog> -->
    </div>
</template>

<script>
import Jszip from 'jszip'
export default {
    name: 'test',
    data () {
        return {
            a: '',
            dialogVisible: true
        }
    },
    created () {
        this.http.getCode()
    },
    methods: {
        file () {
            let ele = document.getElementsByClassName('file')[0]
            let newZip = new Jszip()
            for (let i = 0; i < ele.files.length; i++) {
                newZip.file(ele.files[i].webkitRelativePath, ele.files[i])
            }
            newZip.generateAsync({
                type: 'blob',
                mimeType: 'application/zip',
                compression: 'DEFLATE'
            }).then(function (odsFile) {
                let headers = {

                }
                let oData = new FormData()
                oData.append('folder', odsFile)
                this.http.uploadFile(headers, oData).then(res => {

                })
            })
        }
    }
}
</script>

<style scoped>

</style>



